@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
/* No CSS */
/* No CSS */
.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6rem;
}

.gpt3__navbar-links {
  flex: 1 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.gpt3__navbar-links_logo {
  margin-right: 2rem;
}
.gpt3__navbar-links_logo > img {
  width: 62px;
  height: 16px;
}
.gpt3__navbar-links_container {
  display: flex;
}
.gpt3__navbar-sign {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
  color: #fff;
  font-family: var(--font-family);
  font-weight: bold;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;

  margin: 0 1rem;
  /* cursor: pointer; */
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;

  color: #ffffff;
  padding: 0.5rem 1rem;

  background: #ff4820;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.gpt3__navbar-menu {
  margin-left: 1rem;

  display: none;
  position: relative;
}
.gpt3__navbar-menu svg {
  cursor: pointer;
}

.gpt3__navbar-menu_container {
  background-color: var(--color-footer);

  position: absolute;
  right: 0;
  top: 40px;
  width: -webkit-max-content;
  width: max-content;
  padding: 1rem 2rem;
  text-align: right;
}

.gpt3__navbar-menu_container p {
  margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign {
  display: none;
}
@media screen and (max-width: 1050px) {
  .gpt3__navbar-links_container {
    display: none;
  }
  .gpt3__navbar-menu {
    display: flex;
  }
}
@media screen and (max-width: 700px) {
  .gpt3__navbar {
    padding: 2rem 4rem;
  }
}
@media screen and (max-width: 550px) {
  .gpt3__navbar {
    padding: 2rem;
  }
  .gpt3__navbar-sign {
    display: none;
  }
  .gpt3__navbar-menu_container-links-sign {
    display: block;
  }
}

/* No CSS */
.gpt3__brand {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.gpt3__brand div {
  flex: 1 1;
  min-width: 120px;
  max-width: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
}

/* No CSS */
.gpt3__features {
  width: 100%;
  display: flex;
  margin: 1rem 0;
}
.gpt3__features-title {
  flex: 1 1;
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 24px;
  line-height: 75px;

  letter-spacing: -0.04em;

  color: #ffffff;
}
.gpt3__feature-text {
  flex: 2 1;
  font-family: var(--font-family);

  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  /* or 187% */

  color: #81afdd;
}
.gpt3__features-line {
  height: 3px;
  width: 40px;
  background: var(--gradient-text);
}

@media screen and (max-width: 700px) {
  .gpt3__features {
    flex-direction: column;
  }
}

/* No CSS */
.gpt3__header {
  display: flex;
}
.gpt3__header-content {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin: 5rem;
}
.gpt3__header-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04rem;
}
.gpt3__header-content p {
  font-family: var(--font-family);

  font-weight: normal;
  font-size: 20px;
  line-height: 27px;

  color: var(--color-text);
  margin: 2rem 0;
}
.gpt3__header-content__input {
  width: 100%;
  margin-bottom: 1.5rem;
  display: flex;
}
.gpt3__header-content__input input {
  flex: 7 1;
  width: 100%;

  padding: 1rem;
  border: none;
  border-radius: 5px 0 0 5px;
  background: #052d56;

  font-family: var(--font-family);
  font-weight: normal;
  font-size: 20px;
  line-height: 27px;
  color: #ffffff;
}
.gpt3__header-content__input button {
  flex: 3 1;
  width: 100%;

  background: #ff4820;
  border-radius: 5px 0px 0px 5px;
  transform: matrix(-1, 0, 0, 1, 0, 0);
  font-family: var(--font-family);

  font-weight: bold;
  font-size: 20px;
  line-height: 27px;
  border: none;

  color: #ffffff;
}
.gpt3__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.gpt3__header-content__people img {
  width: 181.79px;
  height: 38px;
}
.gpt3__header-content__people p {
  margin: 0 1rem;
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #ffffff;
  text-align: center;
}
.gpt3__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.gpt3__header-image {
  flex: 1 1;
  justify-content: center;
  align-items: center;
}
.gpt3__header-image img {
  width: 100%;
  /* height: 100%; */
}
@media screen and (max-width: 1050px) {
  .gpt3__header {
    flex-direction: column;
  }

  .gpt3__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__header-content h1 {
    font-size: 48px;
    line-height: 60px;
  }

  .gpt3__header-content p {
    font-size: 16px;
    line-height: 24px;
  }

  .gpt3__header-content__people {
    flex-direction: column;
  }

  .gpt3__header-content__people p {
    margin: 0;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 490px) {
  .gpt3__header-content h1 {
    font-size: 36px;
    line-height: 48px;
  }

  .gpt3__header-content p {
    font-size: 14px;
    line-height: 24px;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 12px;
    line-height: 16px;
  }
}

/* No CSS */
.gpt3__whatgpt3 {
  color: #ccc;
  display: flex;
  flex-direction: column;
  padding: 2rem;
}

.gpt3__whatgpt3-heading {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 4rem 0;
}
.gpt3__whatgpt3-container .gpt3__features {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.gpt3__whatgpt3-container .gpt3__features:nth-child(2) {
  margin: 1rem;
}
@media screen and (max-width: 700px) {
  .gpt3__whatgpt3-container .gpt3__features:nth-child(2) {
    margin: 1rem 0;
  }
}

.gpt3__whatgpt3-heading h1 {
  max-width: 510px;
  font-family: var(--font-family);

  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
}
.gpt3__whatgpt3-heading p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;

  color: #ff8a71;
}
.gpt3__whatgpt3-container {
  display: flex;
}
.gpt3__whatgpt3-feature .gpt3__features-container__feature {
  margin: 0;
}

.gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
  max-width: 700px;
}

@media screen and (max-width: 700px) {
  .gpt3__whatgpt3-feature {
    flex-direction: column;
  }
  .gpt3__whatgpt3-heading {
    flex-direction: column;
  }
  .gpt3__whatgpt3-container {
    flex-direction: column;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}
body {
  background-color: var(--color-bg);
}
a {
  color: unset;
  text-decoration: none;
}
/* nav背景漸層 */
.gradient_bg {
  /* ff 3.6+ */

  /* safari 5.1+,chrome 10+ */

  /* opera 11.10+ */

  /* ie 10+ */

  /* global 92%+ browsers support */
  background: radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 26, 1) 100%);
}
/* nav字體漸層 */
.gradient_text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section_padding {
  padding: 4rem 6rem;
}
.section_margin {
  margin: 4rem 6rem;
}

/* 動畫往中間縮小 */
.scale-down-center {
  animation: scale-down-center 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
@keyframes scale-down-center {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.5);
  }
}
/* 動畫往中間縮小 */

@media screen and(max-width:700px) {
  .section_padding {
    padding: 4rem;
  }
  .section_margin {
    margin: 4rem;
  }
}
@media screen and(max-width:550px) {
  .section_padding {
    padding: 4rem 2rem;
  }
  .section_margin {
    margin: 4rem 2rem;
  }
}

:root {
  --font-family: "Manrope", sans-serif;

  --gradient-text: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #ae67fa -13.86%, #f49867 99.55%);

  --color-bg: #040c18;
  --color-footer: #031b34;
  --color-blog: #042c54;
  --color-text: #81afdd;
  --color-subtext: #ff8a71;
}

