.gpt3__header {
  display: flex;
}
.gpt3__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin: 5rem;
}
.gpt3__header-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04rem;
}
.gpt3__header-content p {
  font-family: var(--font-family);

  font-weight: normal;
  font-size: 20px;
  line-height: 27px;

  color: var(--color-text);
  margin: 2rem 0;
}
.gpt3__header-content__input {
  width: 100%;
  margin-bottom: 1.5rem;
  display: flex;
}
.gpt3__header-content__input input {
  flex: 7;
  width: 100%;

  padding: 1rem;
  border: none;
  border-radius: 5px 0 0 5px;
  background: #052d56;

  font-family: var(--font-family);
  font-weight: normal;
  font-size: 20px;
  line-height: 27px;
  color: #ffffff;
}
.gpt3__header-content__input button {
  flex: 3;
  width: 100%;

  background: #ff4820;
  border-radius: 5px 0px 0px 5px;
  transform: matrix(-1, 0, 0, 1, 0, 0);
  font-family: var(--font-family);

  font-weight: bold;
  font-size: 20px;
  line-height: 27px;
  border: none;

  color: #ffffff;
}
.gpt3__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.gpt3__header-content__people img {
  width: 181.79px;
  height: 38px;
}
.gpt3__header-content__people p {
  margin: 0 1rem;
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #ffffff;
  text-align: center;
}
.gpt3__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.gpt3__header-image {
  flex: 1;
  justify-content: center;
  align-items: center;
}
.gpt3__header-image img {
  width: 100%;
  /* height: 100%; */
}
@media screen and (max-width: 1050px) {
  .gpt3__header {
    flex-direction: column;
  }

  .gpt3__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__header-content h1 {
    font-size: 48px;
    line-height: 60px;
  }

  .gpt3__header-content p {
    font-size: 16px;
    line-height: 24px;
  }

  .gpt3__header-content__people {
    flex-direction: column;
  }

  .gpt3__header-content__people p {
    margin: 0;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 490px) {
  .gpt3__header-content h1 {
    font-size: 36px;
    line-height: 48px;
  }

  .gpt3__header-content p {
    font-size: 14px;
    line-height: 24px;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 12px;
    line-height: 16px;
  }
}
